 margin-left: $slider-side-separation;\n  }\n}\n\n.knob {\n  position: relative;\n  top: 0;\n  left: 0;\n  z-index: $z-index-higher;\n  display: flex;\n  width: $slider-knob-size;\n  height: $slider-knob-size;\n  flex-direction: row;\n  align-items: center;\n  justify-content: center;\n  background-color: transparent;\n}\n\n.innerknob {\n  z-index: $z-index-high;\n  width: $slider-inner-knob-size;\n  height: $slider-inner-knob-size;\n  background-color: $slider-main-color;\n  border-radius: 50%;\n  transition-timing-function: $animation-curve-default;\n  transition-duration: .1s;\n  transition-property: height, width, background-color, border;\n}\n\n.snaps {\n  position: absolute;\n  top: $slider-knob-size / 2 - $slider-snap-size / 2;\n  left: 0;\n  display: flex;\n  width: calc(100% + #{$slider-snap-size});\n  height: $slider-snap-size;\n  flex-direction: row;\n  pointer-events: none;\n  &:after {\n    display: block;\n    width: $slider-snap-size;\n    height: $slider-snap-size;\n    content: \"\";\n    background-color: $slider-snap-color;\n    border-radius: 50%;\n  }\n}\n\n.snap {\n  flex: 1;\n  &:after {\n    display: block;\n    width: $slider-snap-size;\n    height: $slider-snap-size;\n    content: \"\";\n    background-color: $slider-snap-color;\n    border-radius: 50%;\n  }\n}\n\n.input {\n  width: $slider-input-width;\n  padding: 0;\n  margin-bottom: 0;\n  > input {\n    text-align: center;\n  }\n}\n\n.progress {\n  position: absolute;\n  top: 0;\n  left: $slider-knob-size / 2;\n  width: 100%;\n  height: 100%;\n  .innerprogress {\n    position: absolute;\n    top: $slider-knob-size / 2 - $slider-bar-height / 2;\n    height: $slider-bar-height;\n    [data-ref=\"value\"] {\n      transition-duration: 0s;\n    }\n  }\n}\n\n.slider {\n  &:focus .knob:before {\n    position: absolute;\n    top: 0;\n    right: 0;\n    bottom: 0;\n    left: 0;\n    z-index: $z-index-normal;\n    content: \"\";\n    background-color: $slider-main-color;\n    border-radius: 50%;\n