\n@import \"./config\";\n@import \"./mixins\";\n\n.button {\n  position: relative;\n\n  > input {\n    position: absolute;\n    top: 0;\n    left:0;\n    z-index: 0;\n    width: 0.1px;\n    height: 0.1px;\n    padding: 0;\n    margin: 0;\n    overflow: hidden;\n    opacity: 0;\n  }\n}\n\n%button {\n  @include typo-button();\n  position: relative;\n  display: inline-block;\n  height: $button-height;\n  flex-direction: row;\n  align-content: center;\n  align-items: center;\n  justify-content: center;\n  line-height: $button-height;\n  text-align: center;\n  text-decoration: none;\n  white-space: nowrap;\n  cursor: pointer;\n  border: 0;\n  outline: none;\n  transition: box-shadow .2s $animation-curve-fast-out-linear-in, background-color .2s $animation-curve-default, color .2s $animation-curve-default;\n  &::-moz-focus-inner {\n    border: 0;\n  }\n  > span:not([data-react-toolbox=\"tooltip\"]) {\n    display: inline-block;\n    line-height: $button-height;\n    vertical-align: top;\n  }\n  > svg {\n    display: inline-block;\n    width: 1em;\n    height: $button-height;\n    font-size: 120%;\n    vertical-align: top;\n    fill: currentColor;\n  }\n  > * {\n    pointer-events: none;\n  }\n  > .rippleWrapper {\n    overflow: hidden;\n  }\n  &[disabled] {\n    color: $button-disabled-text-color;\n    pointer-events: none;\n    cursor: auto;\n  }\n}\n\n%squared {\n  min-width: $button-squared-min-width;\n  padding: $button-squared-padding;\n  border-radius: $button-border-radius;\n  .icon {\n    margin-right: $button-squared-icon-margin;\n    font-size: 120%;\n    vertical-align: middle;\n  }\n  > svg {\n    margin-right: .5 * $unit;\n  }\n}\n\n%solid {\n  &[disabled] {\n    @include shadow-2dp();\n    background-color: $button-disabled-background-color;\n  }\n  &:active {\n    @include shadow-4dp();\n  }\n  &:focus:not(:active) {\n    @include focus-shadow();\n  }\n}\n\n.raised {\n  @extend %button;\n  @extend %squared;\n  @extend %solid;\n  @include shadow-2dp();\n}\n\n.flat {\n  @extend %button;\n  @extend %squared;\n  background: transparent;\n}\n\n.floating {\n  @extend %button;\n  @extend %solid;\n  width: $button-floating-height;\n  height: $button-floating-height;\n  font-size: $button-floating-font-size;\n  border-radius: 50%;\n  box-shadow: 0 1px 1.5px 0 rgba(0, 0, 0, .12), 0 1px 1px 0 rgba(0, 0, 0, .24);\n  .icon {\n    line-height: $button-floating-height;\n  }\n  > .rippleWrapper {\n    border-radius: 50%;\n  }\n  &.mini {\n    width: $button-floating-mini-height;\n    height: $button-floating-mini-height;\n    font-size: $button-floating-mini-font-size;\n    .icon {\n      line-height: $button-floating-mini-height;\n    }\n  }\n}\n\n.toggle {\n  @extend %button;\n  width: $button-height;\n  background: transparent;\n  border-radius: 50%;\n  > .icon, svg {\n    font-size: $button-toggle-font-size;\n    line-height: $button-height;\n    vertical-align: top;\n  }\n  > .rippleWrapper {\n    border-radius: 50%;\n  }\n}\n\n.neutral:not([disabled]) {\n  &.raised, &.floating {\n    color: $button-neutral-color-contrast;\n    background-color: $button-neutral-color;\n  }\n  &.flat, &.toggle {\n    color: $button-neutral-color-contrast;\n    &:focus:not(:active) {\n      background: $button-neutral-color-hover;\n    }\n  }\n  &.flat:hover {\n    background: $button-neutral-color-hover;\n  }\n\n  &.inverse {\n    &.raised, &.floating {\n      color: $button-neutral-color;\n      background-color: $button-neutral-color-contrast;\n    }\n    &.flat, &.toggle {\n      color: $button-neutral-color;\n      &:focus:not(:active) {\n        background: $button-neutral-color-hover;\n      }\n    }\n    &.flat:hover {\n      background: $button-neutral-color-hover;\n    }\n  }\n}\n\n@include btn-colors(\"primary\", $button-primary-color-contrast, $button-primary-color, $button-primary-color-hover);\n@include btn-colors(\"accent\", $button-accent-color-contrast, $button-accent-color, $button-accent-color-hover);\n","// scss-lint:disable VendorPrefix\n@mixin typo-preferred-font($use-preferred: true) {\n  @if $use-preferred {\n    font-family: $preferred-font;\n  }\n}\n\n@mixin typo-display-4($color-contrast: false, $use-preferred: true) {\n  @include typo-preferred-font($use-preferred);\n  font-size: $unit * 11.2;\n  font-weight: 300;\n  line-height: 1;\n  letter-spacing: -.04em;\n\n  @if $color-contrast {\n    opacity: .54;\n  }\n}\n\n@mixin typo-display-3($color-contrast: false, $use-preferred: true) {\n  @include typo-preferred-font($use-preferred);\n  font-size: $unit * 5.6;\n  font-weight: 400;\n  line-height: 1.35;\n  letter-spacing: -.02em;\n\n  @if $color-contrast {\n    opacity: .54;\n  }\n}\n\n@mixin typo-display-2($color-contrast: false, $use-preferred: true) {\n  @include typo-preferred-font($use-preferred);\n  font-size: $unit * 4.5;\n  font-weight: 400;\n  line-height: $unit * 4.8;\n\n  @if $color-contrast {\n    opacity: .54;\n  }\n}\n\n@mixin typo-display-1($color-contrast: false, $use-preferred: true) {\n  @include typo-preferred-font($use-preferred);\n  font-size: $unit * 3.4;\n  font-weight: 400;\n  line-height: $unit * 4;\n\n  @if $color-contrast {\n    opacity: .54;\n  }\n}\n\n@mixin typo-headline($color-contrast: false, $use-preferred: true) {\n  @include typo-preferred-font($use-preferred);\n  font-size: $unit * 2.4;\n  font-weight: 400;\n  line-height: $unit * 3.2;\n  -moz-osx-font-smoothing: grayscale;\n\n  @if $color-contrast {\n    opacity: .87;\n  }\n}\n\n@mixin typo-title($color-contrast: false, $use-preferred: true) {\n  @include typo-preferred-font($use-preferred);\n  font-size: $unit * 2;\n  font-weight: 500;\n  line-height: 1;\n  letter-spacing: .02em;\n\n  @if $color-contrast {\n    opacity: .87;\n  }\n}\n\n@mixin typo-subhead($color-contrast: false, $use-preferred: true) {\n  @include typo-preferred-font($use-preferred);\n  font-size: $unit * 1.6;\n  font-weight: 400;\n  line-height: $unit * 2.4;\n  letter-spacing: .04em;\n\n  @if $color-contrast {\n    opacity: .87;\